import React, { forwardRef } from "react";
import styled from "styled-components";

import { Root } from "@radix-ui/react-tabs";

import { Margins, marginsCss } from "../styleguide";

import { TabsProps } from "./Tabs.types";

const StyledRoot = styled(Root)<Margins>`
	${marginsCss}
`;

/**
 * @description Tabs is built on top of [@radix-ui/react-tabs](https://www.radix-ui.com/docs/primitives/components/tabs)
 * @see https://www.radix-ui.com/docs/primitives/components/tabs
 */
const Tabs = forwardRef<HTMLDivElement, TabsProps>(({
	children,
	dataQA,
	...props
}, ref) => (
	<StyledRoot
		ref={ref}
		data-qa={dataQA}
		{...props}
	>
		{children}
	</StyledRoot>
));

Tabs.displayName = "Tabs";

export default Tabs;
